import i18next from "./lang/i18n";

const isPortrait = window.matchMedia('(orientation: portrait)').matches;

const COLORS = {
    customBlue3: '#556273',
    customBlue2: '#69788c',
    customBlue1: '#8a99a6',
    customGray: '#d9d9d9',
    customWhite: '#fafafa',

    bisque: '#d7c2b0',
    copper: '#9d442a',
    darkTeal: '#0c4f4e',
    dustyGold: '#bc804d',
    grove: '#9eb5a3',

    eucalyptus: '#a8b6af',
    enchanted: '#47505e',
    pine: '#5a716c',
    mistyMint: '#e7eeea',
    gold: '#c9a479',

    lightGold: '#e4cdaf',
};

const FONT_SIZE = {
    titleText3: isPortrait ? '3.0rem' : '7rem',
    titleText2: isPortrait ? '1.8rem' : '3.5rem',
    titleText1: isPortrait ? '1.6rem' : '2.5rem',
    bodyText2: isPortrait ? '1.3rem' : '2.2rem',
    bodyText1: isPortrait ? '1.1rem' : '1.9rem',
    smallText: isPortrait ? '1.0rem' : '1.6rem',
    smallText2: isPortrait ? '0.9rem' : '1.2rem',
    smallText3: isPortrait ? '0.5rem' : '0.9rem'
}

enum FONTS {
    PALATINO_LINOTYPE= 'Palatino Linotype',
    EB_GARAMOND= 'EB Garamond',
    MERRIWEATHER= 'Merriweather',
    LATO= 'Lato',
    ALEX_BRUSH= 'Alex Brush',
    ARIAL= 'Arial',
    MARU_BURI= 'MaruBuri',
    S_HANGANG_J_L='seoul-hangang-jung-l'
}

const getI18nFont = (enFont: FONTS, koFont: FONTS): string => {
    if (i18next.resolvedLanguage === 'ko') return koFont;
    if (i18next.resolvedLanguage === 'en') return enFont;
    return enFont;
}

const isEn = (): boolean => {
    return i18next.resolvedLanguage === 'en';
}

export { COLORS, FONT_SIZE, FONTS, getI18nFont, isEn };