type LinkProps = {
  children?: React.ReactNode
  onClick: ()=>void
};

function LinkComponent({children, onClick}: LinkProps) {
  return (
      <>
        <span onClick={onClick} style={{ textDecoration: 'underline' }} >{children}</span>
      </>
  );
}

